.slick-slider-block {
  box-sizing: border-box;
  font-size: 1.1em;
  line-height: 1.6em;
  padding: 0;
  margin: 0; }
  .slick-slider-block.slick-slider-has-dots {
    padding: 10px; }
  .slick-slider-block.slick-slider-has-arrows {
    padding: 20px 30px; }
    .slick-slider-block.slick-slider-has-arrows .slick-list {
      margin: 0 5px; }
      .slick-slider-block.slick-slider-has-arrows .slick-list .slick-track .slick-slide {
        margin: 0 5px; }
  .slick-slider-block.slick-slider-has-shadow .slick-slide {
    box-shadow: 0 5px 10px 5px rgba(186, 186, 186, 0.5);
    margin: 10px 15px 20px 15px !important; }
  .slick-slider-block div:focus {
    outline: none; }
  .slick-slider-block .slick-list {
    margin: 0; }
    .slick-slider-block .slick-list .slick-track {
      display: flex !important; }
      .slick-slider-block .slick-list .slick-track .slick-slide {
        height: inherit !important;
        margin: 0;
        overflow: hidden;
        padding: 20px; }
        .slick-slider-block .slick-list .slick-track .slick-slide .slick-slide-grid {
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;
          width: 100%; }
          .slick-slider-block .slick-list .slick-track .slick-slide .slick-slide-grid h3 {
            font-size: 1.4em;
