          .slick-slider-block .slick-list .slick-track .slick-slide .slick-slide-grid p, .slick-slider-block .slick-list .slick-track .slick-slide .slick-slide-grid ul {
            margin: 0 0 20px; }
          .slick-slider-block .slick-list .slick-track .slick-slide .slick-slide-grid .slick-slide-column:first-of-type {
            flex: 0;
            margin-right: 20px;
            min-width: 200px; }
          .slick-slider-block .slick-list .slick-track .slick-slide .slick-slide-grid .slick-slide-column {
            display: flex;
            flex-direction: column;
            flex: 1; }
        .slick-slider-block .slick-list .slick-track .slick-slide .cn-image-style {
          display: block;
          margin: 0 0 8px; }
        .slick-slider-block .slick-list .slick-track .slick-slide span.title {
