        .slick-slider-block .slick-list .slick-track .slick-slide span.phone-number-block,
        .slick-slider-block .slick-list .slick-track .slick-slide span.email-address-block {
          display: block;
          font-size: .9em; }
        .slick-slider-block .slick-list .slick-track .slick-slide span.email-address {
          -ms-word-break: break-all;
          word-break: break-all;
          word-break: break-word;
          /* Non standard for webkit */
          -webkit-hyphens: auto;
          -ms-hyphens: auto;
          hyphens: auto; }
        .slick-slider-block .slick-list .slick-track .slick-slide span.social-media-block {
          margin: 10px 0 0; }
        .slick-slider-block .slick-list .slick-track .slick-slide .cn-excerpt {
          font-size: .95em; }
  .slick-slider-block .slick-slider {
    margin: 0;
    /* Required to keep core WP Editor from displaying the slider navigation text. */ }
    .slick-slider-block .slick-slider button.slick-arrow {
      color: transparent; }
    .slick-slider-block .slick-slider .slick-arrow.slick-prev:before, .slick-slider-block .slick-slider .slick-arrow.slick-next:before {
      opacity: .75;
      color: #000000; }
    .slick-slider-block .slick-slider ul.slick-dots {
      bottom: auto;
      line-height: normal;
      margin: 0;
      padding: 0;
      position: relative; }
      .slick-slider-block .slick-slider ul.slick-dots li button:before {
        opacity: .25;
        color: #000000; }

@media (max-width: 480px) {
  .slick-slider-block {
    font-size: 1em;
    line-height: 1.4em; }
    .slick-slider-block.slick-slider-has-arrows {
      padding: 20px 30px; }
    .slick-slider-block.slick-slider-has-shadow .slick-slide {
      box-shadow: 0 2px 5px 2px rgba(186, 186, 186, 0.5);
