          flex: none;
          margin: 0;
          min-width: auto;
          width: 100%; }
        .slick-slider-block .slick-list .slick-track .slick-slide .slick-slide-grid .cn-image-style span:first-child {
          margin: 0 auto; }
      .slick-slider-block .slick-list .slick-track .slick-slide .cn-excerpt {
        font-size: .9em;
        margin-top: 12px; } }
